import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [

    {
        path: 'evChargingStationFranchise',
        loadChildren: () => import('./EV-charging-station-franchise/EV-charging-station-franchise.module').then(m => m.EVChargingStationFranchiseModule),
        data: { title: 'EV Charging Station Franchise', titleI18n: 'EV-charging-station-franchise' },
    },
    
    {
        path: 'evBatteryManufacturers',
        loadChildren: () => import('./EV-battery-manufacturers/EV-battery-manufacturers.module').then(m => m.EVBatteryManufacturersModule),
        data: { title: 'EV Battery Manufactures', titleI18n: 'EV Battery Manufactures' },
    },

    {
        path: 'evDealerShipFranchise',
        loadChildren: () => import('./EV-dealership-franchise-provider/EV-dealership-franchise-provider.module').then(m => m.EVChargingStationFranchiseModule),
        data: { title: 'EV Dealership Franchise Provider', titleI18n: 'EV Dealership Franchise Provider' },
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class EVDealersRoutingModule { }